p,
ul,
ol,
dl,
a {
  font-family: 'Lora', serif;
  font-size: 18px;
  line-height: 26px;
}

@media screen and (max-width: 640px) {
  p,
  ul,
  ol,
  dl,
  a {
    font-size: 14px;
    line-height: 22px;
  }
}

a {
  color: #2977cc;
  text-decoration: none;
}

a:hover {
  color: #4d91db;
}

a:visited {
  color: #205ea2;
}

a:visited:hover {
  color: #2977cc;
}

h2 {
  margin-top: 0;
  margin-bottom: 36px;
  font-family: 'Sacramento', cursive;
  font-size: 42px;
  text-align: center;
  font-weight: 400;
}

@media screen and (max-width: 640px) {
  h2 {
    font-size: 32px;
    margin-bottom: 22px;
  }
}

@media screen and (max-width: 420px) {
  h2 {
    margin-top: 20px;
    margin-bottom: 24px;
  }
}

.headline-break {
  display: none;
}

@media screen and (max-width: 470px) {
  .headline-break {
    display: inherit;
  }
}

dt {
  font-weight: bold;
}

dd {
  margin: 14px 0 0;
}

@media screen and (max-width: 640px) {
  dd {
    margin-top: 10px;
  }
}

dd + dt {
  margin-top: 36px;
}

@media screen and (max-width: 640px) {
  dd + dt {
    margin-top: 28px;
  }
}

body {
  background: #ddd url(../images/bg.svg) no-repeat center -56px;
  background-size: 4096px 1152px;
  color: #2a2829;
  margin: 0;
}

@media screen and (max-width: 1050px) {
  body {
    background-position: center -35px;
    background-size: 2560px 720px;
  }
}

@media screen and (max-width: 420px) {
  body {
    background-position: center -28px;
    background-size: 2048px 576px;
  }
}

header {
  position: relative;
  height: 256px;
  overflow: auto;
  text-align: center;
}

@media (max-width: 1050px) {
  header {
    height: 160px;
  }
}

@media (max-width: 420px) {
  header {
    height: 128px;
  }
}

.monogram {
  position: relative;
  margin: 0;
  top: 59px;
}

@media (max-width: 1050px) {
  .monogram {
    top: 35px;
  }
}

@media (max-width: 420px) {
  .monogram {
    top: 26px;
  }
}

.monogram__image {
  width: 200px;
}

@media (max-width: 1050px) {
  .monogram__image {
    width: 130px;
  }
}

@media (max-width: 420px) {
  .monogram__image {
    width: 110px;
  }
}

main {
  position: relative;
  box-sizing: border-box;
  max-width: 960px;
  margin: 0 auto 40px;
  padding: 80px 140px 80px;
  background-color: white;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2), inset 0 0 10px rgba(0, 0, 0, 0.03);
}

@media screen and (max-width: 1050px) {
  main {
    width: calc(100% - ($margin-side * 2));
    margin: 0 30px 40px;
    padding: 60px;
  }
}

@media screen and (max-width: 640px) {
  main {
    width: calc(100% - ($margin-side * 2));
    margin: 0 20px 40px;
    padding: 40px;
  }
}

@media screen and (max-width: 420px) {
  main {
    width: calc(100% - ($margin-side * 2));
    margin: 0 10px 40px;
    padding: 20px;
  }
}

main.letter {
  margin-bottom: 130px;
}

@media screen and (max-width: 1050px) {
  main.letter {
    margin-bottom: 140px;
  }
}

@media screen and (max-width: 770px) {
  main.letter {
    margin-bottom: 140px;
  }
}

@media screen and (max-width: 420px) {
  main.letter {
    margin-bottom: 120px;
  }
}

@media screen and (max-width: 380px) {
  main.letter {
    margin-bottom: 356px;
  }
}

dl {
  margin-bottom: 46px;
}

@media screen and (max-width: 640px) {
  dl {
    margin-bottom: 32px;
  }
}

.valediction {
  display: flex;
}

@media screen and (max-width: 380px) {
  .valediction {
    flex-wrap: wrap-reverse;
  }
}

.photo {
  position: relative;
  width: 330px;
}

@media screen and (max-width: 536px) {
  .photo {
    width: 244px;
  }
}

.photo__border {
  position: absolute;
  top: 42px;
  left: -10px;
  width: 330px;
  box-sizing: border-box;
  padding: 30px;
  background-color: white;
  -webkit-transform: rotate(-5deg);
          transform: rotate(-5deg);
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2), inset 0 0 10px rgba(0, 0, 0, 0.03);
}

@media screen and (max-width: 1050px) {
  .photo__border {
    left: 46px;
  }
}

@media screen and (max-width: 770px) {
  .photo__border {
    left: 0;
  }
}

@media screen and (max-width: 536px) {
  .photo__border {
    width: 280px;
    left: -56px;
  }
}

@media screen and (max-width: 380px) {
  .photo__border {
    top: -38px;
    left: 20px;
  }
}

@media screen and (max-width: 360px) {
  .photo__border {
    left: -28px;
  }
}

.photo__image {
  width: 100%;
}

.signature-block {
  width: calc(100% - 360px);
  margin: 136px 30px 60px 0;
  text-align: center;
}

@media screen and (max-width: 380px) {
  .signature-block {
    margin: 40px 0 60px 40%;
  }
}

.signature-block__signature {
  display: inline-block;
  margin-left: 40px;
  font-family: 'Sacramento', cursive;
  font-size: 48px;
  line-height: 77px;
  -webkit-transform: rotate(-5deg);
          transform: rotate(-5deg);
}

@media screen and (max-width: 580px) {
  .signature-block__signature {
    margin-left: 14px;
  }
}

@media screen and (max-width: 470px) {
  .signature-block__signature {
    font-size: 38px;
    line-height: 77px;
  }
}

footer {
  margin: 0 0 40px;
  text-align: center;
  color: #919191;
  text-shadow: 0 1px 0 white;
}

.browserupgrade {
  margin: 0.2em 0;
  background: #ccc;
  color: #000;
  padding: 0.2em 0;
}
